<template>
  <div id="avatar-wrapper" :style="{ height: height + 'px', width : width + 'px'}">

    <div id="divVHSS"></div>

  </div>
</template>

<script>
import {EventBus} from "@/event-bus";
//import {startRecognition,stopRecognition} from "@/components/speechRecognition"


export default {
  name: "avatar",
  props: {
    initialWidth: {
      type: Number,
      required: true
    }
  },

  data() {
    return {
      width: this.initialWidth,
    }
  },
  computed: {
    height: {
      get: function () {
        return this.width * 1.125;
      },
      set: function (newVal) {
        this.width = newVal * 8 / 9;
      }
    },
    spVoice: function () {
      switch (this.curLanguage) {
        case 'es':
          return 4;
        default: // en
          return 3;
      }
    },
    spLang: function () {
      switch (this.curLanguage) {
        case 'es':
          return 2;
        default: // en
          return 1;
      }
    },
    spEngine: function () {
      switch (this.curLanguage) {
        case 'es':
          return 4;
        default: // en
          return 3;
      }
    }
  },
  methods: {
    emitGlobalImageCloseEvent(){
      EventBus.$emit('closeImageDialog');
    },
    avatarResize() {
      console.log('New Width is ', this.width)
      console.log('New Height is', this.height)
      window.dynamicResize(this.width, this.height)
    },
    speak(speakText) {
      console.log('Speeching Text', speakText)
      window.sayText('<prosody rate="+15%">' + speakText + '</prosody>', 3, 1, 3);
      return this.promise;
    },

    stopSpeak() {
      window.stopSpeech()
    }
  },
  watch: {
    width: function () {
      window.dynamicResize(this.width, this.height);
      console.log('canvas canvas canvas')
    },
  },
  mounted() {
    const that = this
    window.vh_sceneLoaded = function () {
      console.log('It came in here')
      that.avatarResize()
    },
    window.vh_talkStarted = function () {
      that.$store.commit('updateIsSpeaking', true)
      //stopRecognition()
    },
    window.vh_talkEnded = function (){
      //startRecognition()
      console.log('Talk Ended')
      that.$store.commit('updateIsSpeaking', false)
     // that.$store.commit('updateUserSpeechInput', "I am Listening. You can Ask me a Question")
      that.emitGlobalImageCloseEvent();
    }
  }
}
</script>

